.project-grid {
    padding: 5vh 5vw 5vh 5vw;
    display: grid;
    grid-gap: 1rem;
    gap: 1rem;
  
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-auto-rows: 300px;
}

.grid-item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    position: relative;
	object-fit: cover;
	width: 100%;
	height: 100%;

    color: #ECECEC;
}

.grid-item-overlay {
    background-color: #222831;
    opacity: 0;
    color: white;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: left;
            justify-content: left;
    -webkit-align-items: center;
            align-items: center;

    position: absolute;
    top: 0;
    bottom: 0px;
    left: 0;
    right: 0;
}

.grid-item-overlay:hover {
    opacity: 0.8;
}

.grid-item:hover .grid-item-title {
